.LogOut{
	font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

@media screen and (min-width: 768px){
	.LogOut{
		background-color: var(--color-A);
		color: var(--color-A2);
		height: 50%;
	}
	.LogOut:active{
		background-color: var(--color-B);
		color: var(--color-A3);
	}
}